import styled from "styled-components";
import palette from "../../lib/styles/palette";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/all";
import { useEffect, useRef } from "react";


const Wrapper = styled.div`
    position: relative;
    display: block;
    padding: 2rem;
    justify-content: space-evenly;
    align-items: center;
    min-height: 80vh;
    margin-top: 10rem;
    margin-bottom: 10rem;
    /* background-color: ${palette.gray[1]}; */
`;

const TextList = styled.div`
    .name {
        font-size: 3rem;
    }
    .field {
        font-size: 2rem;
    }
    .sub {
        font-size: 1.5rem;
    }
    & > * {
        margin: 2rem;
    }
`;
const DetailTextWrapper = styled.div`
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    & > * {
        width: 50%;
    }
    
`;

const ProfileContainer = () => {
    // TO-DO : Json 
    const expers = [
        '  - PHM, Offline_Chat',
        '  - WebOS',
        'SJSU educate program - Petmily',
        '   (C, Python, R)',
        ' ,   ',
    ];
    const techs = [
        'HTML, CSS, JavaScript ( ,  )',
        'React (, Toy )',
        'Python (PHM )',
        'C, C++ (Unity Toy, PHM )',
        'Java ( )',
        'Flutter ( )'
    ];

    gsap.registerPlugin(ScrollTrigger);
    const nameRef = useRef();
    const leftListRef = useRef();
    const rightListRef = useRef();

    const nameGsap = gsap.utils.selector(nameRef);
    const leftListGsap = gsap.utils.selector(leftListRef);
    const rightListGsap = gsap.utils.selector(rightListRef);
    useEffect(() => {
        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: nameRef.current,
                toggleActions: "play none none none",
            }
        })

        tl.from(nameGsap("div"), {
            opacity: 0,
            stagger: 0.3,
        })
        .from(leftListGsap("div"), {
            y: 100,
            opacity: 0,
            stagger: 0.1,
        })
        .from(rightListGsap("div"), {
            y: 100,
            opacity: 0,
            stagger: 0.1,
        });
    }, [nameGsap, leftListGsap, rightListGsap])



    return (
        <Wrapper>
            <TextList ref={nameRef}>
                <div className="name"></div>
                <div className="field">   </div>
                <div><hr /></div>
                <div>        </div>
                <div>    </div>
            </TextList>
            <DetailTextWrapper>
                <TextList ref={leftListRef}>
                    <div className="sub"> </div>
                    {expers.map(exper => (
                        <div key={exper}>{exper}</div>
                    ))}
                </TextList>
                <TextList ref={rightListRef}>
                    <div className="sub"> </div>
                    {techs.map(tech => (
                        <div key={tech}>{tech}</div>
                    ))}
                </TextList>
            </DetailTextWrapper>
           
        </Wrapper>
    );
};

export default ProfileContainer;